  height: 20px;
  margin: 30px auto 5px auto;
}
#human_auth_container .groupSeparator {
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.7);
  font-weight: bold;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  opacity: 0.58;
  right: 0;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
  width: 80%;
  margin-top: -13px;
  display:block;
}
