  overflow: hidden;
  text-align: center;
  font-family: 'Helvetica';
  font-size: 12px;
}

#human_auth_container .groupSeparator p:before, .groupSeparator p:after {
    content: "";
    display: inline-block;
    height: 1px;
    position: relative;
    vertical-align: middle;
    width: 40%;
}
#human_auth_container .groupSeparator p::before{
  margin-right: 4px;
  background: #222;
  box-shadow: 0 1px 0 rgba(200, 200, 200, 0.48);
  display: inline-block;
  opacity: 0.58;
}
#human_auth_container .groupSeparator p::after{
  margin-left: 4px;
  height: 1px;
  position: relative;
  vertical-align: middle;
  width: 40%;
  background: #222;
  box-shadow: 0 1px 0 rgba(200, 200, 200, 0.48);
  display: inline-block;
  opacity: 0.58;
}


